@import url("https://fonts.googleapis.com/css?family=Nanum+Myeongjo");
@import url("https://fonts.googleapis.com/css?family=Quicksand");
.Header {
  position: absolute;
  width: 100%;
  height: 60px;
  background-color: #20232a;
  top: 0; }
  .Header .title {
    display: inline-block;
    line-height: 60px;
    font-family: 'Quicksand', sans-serif;
    color: #61dafb;
    font-size: 1.5em;
    vertical-align: top; }
  .Header .hreflink {
    position: absolute;
    top: 0;
    right: 3rem;
    font-size: 0.8rem;
    line-height: 60px;
    color: white; }
    .Header .hreflink:hover {
      color: #61dafb; }
  .Header ul {
    display: inline-block;
    position: absolute;
    top: 0;
    color: white;
    line-height: 60px;
    padding: 0;
    padding-left: 5rem; }
    .Header ul li {
      display: inline-block;
      list-style: none; }
      .Header ul li a {
        display: inline-block;
        padding: 0 1rem;
        line-height: 60px;
        height: 60px;
        font-family: 'Quicksand', sans-serif; }
        .Header ul li a:hover {
          color: #61dafb; }
        .Header ul li a.active {
          border-bottom: 4px solid #61dafb;
          color: #61dafb;
          cursor: pointer; }

.Header + section {
  margin-top: 60px;
  text-align: center; }

@media screen and (max-width: 724px) {
  .Header ul {
    display: none; } }



